import { createSlice, createAsyncThunk } from "@reduxjs/toolkit"; // Redux Toolkit, createAsyncThunk

export const messageSlice = createSlice({
  name: 'message',
  initialState: [

  ],
  reducers: {  // action
    createMessage(state, action) { // createTodo() state(Slice), action()

      console.log('createMessage action:',action);
      console.log('createMessage payload:',action.payload);

      if (action.payload.success) {
        state.push({
          id: action.payload.id,
          type: 'success', // 
          title: '', // 
          text: action.payload.message, // 
        });
      } else {
        state.push({
          id: action.payload.id,
          type: 'danger',
          title: '',
          text: Array.isArray(action.payload?.message) // isArray
            ? action.payload?.message.join('')
            : action.payload?.message,
        });
      }

    /*setTimeout(() => {
        // state  
        const index = state.findIndex(item => item === id);
        state.splice(index, 1);
      }, 2000); */

    },
    removeMessage(state, action) {
      console.log('removeMessage:', action.payload);
      const index = state.findIndex(item => item === action.payload);
      state.splice(index, 1);
    }
  }
});

// , 
// createAsyncThunk , 
// , sync function
export const createAsyncMessage = createAsyncThunk(
  'message/createAsyncMessag', // 1.
  async function (payload, { dispatch, requestId }) {  // 2.async function
    //(payload, params)
      //console.log('createAsyncMessag', payload, params);
      dispatch(messageSlice.actions.createMessage({
        ...payload,
        id: requestId,
      }),
    );

    setTimeout(() => { // 3, 
      dispatch(messageSlice.actions.removeMessage(requestId))
    }, 3000)
  }
);

export const { createMessage } = messageSlice.actions; //  -action

export default messageSlice.reducer;  // -reducer
