import { createContext } from "react";

// useContext 
export const MessageContext = createContext({});

export const initState = {
  type: '',
  title: '',
  text: ''
};

// Reducer
export const messageReducer = (state, action) => {
  switch (action.type) {
    case "POST_MESSAGE" :
      return {
        ...action.payload
      };
    case "CLEAR_MESSAGE" :
      return {
        ...initState,
      };
    default:
      return state;
  }
};

// Reducer messageStore.js ()
export function handleSuccessMessage(dispatch, res) {
  dispatch({
    type: 'POST_MESSAGE',
    payload: {
      type: 'success', // bootstrap
      title: '',
      text: res.data.message,
    }
  });
  setTimeout(() => {
    dispatch({
      type: 'CLEAR_MESSAGE',
    });
  }, 3000);
}

// Reducer messageStore.js ()
export function handleErrorMessage(dispatch, error) {
  dispatch({
    type: 'POST_MESSAGE',
    payload: {
      type: 'danger', // bootstrap
      title: '',
      text: Array.isArray(error?.response?.data?.message) // isArray
        ? error?.response?.data?.message.join('')
        : error?.response?.data?.message,
    },
  });
  setTimeout(() => {
    dispatch({
      type: 'CLEAR_MESSAGE',
    });
  }, 3000);
}