import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { MessageContext, handleSuccessMessage, handleErrorMessage } from "../store/messageStore";

function ProductModal({ closeProductModal, getProducts, type, temProduct}) {
  // 
  const [tempData, setTempData] = useState({
    "title": "",
    "category": "",
    "origin_price": 100,
    "price": 300,
    "unit": "",
    "description": "",
    "content": "",
    "is_enabled": 1,
    "imageUrl": "",
    "imagesUrl": ["", "", "", "", ""]
  });

  const [, dispatch] = useContext(MessageContext); // Reducer messageStore.js ()

  // Modal
  useEffect(() => {
    if (type === 'create') {
      setTempData({
        "title": "",
        "category": "",
        "origin_price": 100,
        "price": 300,
        "unit": "",
        "description": "",
        "content": "",
        "is_enabled": 1,
        "imageUrl": "",
        "imagesUrl": ["", "", "", "", ""]
      });
    } else if (type === 'edit') {
      setTempData({
        ...temProduct,
        //  imagesUrl  undefined
        imagesUrl: Array.isArray(temProduct.imagesUrl) ? temProduct.imagesUrl : ["", "", "", "", ""]
      });
    }
  }, [type, temProduct])

  // 
  const handleChange = (e, index = null) => {

    const { value, name } = e.target;

    //  imagesUrl 
    if (name === 'imagesUrl') {
      setTempData(prevData => {
        const newImagesUrl = [...prevData.imagesUrl]; // 
        newImagesUrl[index] = value; // 
        return {
          ...prevData,
          imagesUrl: newImagesUrl, //  imagesUrl
        };
      });

    } else if (['price', 'origin_price'].includes(name)) {
      setTempData({
        ...tempData,
        [name]: Number(value), // 
      });
    } else if (name === 'is_enabled') {
      setTempData({
        ...tempData,
        [name]: +e.target.checked, // boolean
      });
    } else {
      setTempData({
        ...tempData,
        [name]: value,
      });
    }
  };

  // API-
  const submit = async () => {
    try {

      // API- ()
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`;
      let method = 'post'

      // API-
      if (type === 'edit') {
        api =  `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${temProduct.id}`;
        method = 'put' 
      };

      const res = await axios[method](
        api, {
          data: tempData,
        }

      );
      console.log(res); // API-

      handleSuccessMessage(dispatch, res); // Reducer messageStore.js ()
      closeProductModal(); // Modal
      getProducts(); // API-

    } catch(error) {
      console.log(error);
      handleErrorMessage(dispatch, error); // Reducer messageStore.js ()
    }

  }
  return (
    <div
      id="productModal"
      className='modal fade'
      tabIndex='-1'
      aria-labelledby='exampleModalLabel'
      aria-hidden='true'
    >
      <div className='modal-dialog modal-lg'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h1 className='modal-title fs-5' id='exampleModalLabel'>
              {type === 'create' ? '' : ` ${tempData.title}`}
            </h1>
            <button
              type='button'
              className='btn-close'
              aria-label='Close' 
              onClick={closeProductModal}
            />
          </div>
          <div className='modal-body'>
            <div className='row'>
              <div className='col-sm-4'>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='image'>
                    
                    <input
                      type='text'
                      name='imageUrl'
                      id='imageUrl'
                      placeholder=''
                      className='form-control'
                      onChange={handleChange} 
                      value={tempData.imageUrl}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='image'>
                    1
                    <input
                      type='text'
                      name='imagesUrl'
                      id='image0'
                      placeholder=''
                      className='form-control'
                      onChange={(e) => handleChange(e, 0)} // 
                      value={tempData.imagesUrl[0]} // 
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='image'>
                    2
                    <input
                      type='text'
                      name='imagesUrl'
                      id='image1'
                      placeholder=''
                      className='form-control'
                      onChange={(e) => handleChange(e, 1)} // 
                      value={tempData.imagesUrl[1]} // 
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='image'>
                    3
                    <input
                      type='text'
                      name='imagesUrl'
                      id='image3'
                      placeholder=''
                      className='form-control'
                      onChange={(e) => handleChange(e, 2)}
                      value={tempData.imagesUrl[2]}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='image'>
                    4
                    <input
                      type='text'
                      name='imagesUrl'
                      id='image4'
                      placeholder=''
                      className='form-control'
                      onChange={(e) => handleChange(e, 3)}
                      value={tempData.imagesUrl[3]}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='image'>
                    5
                    <input
                      type='text'
                      name='imagesUrl'
                      id='image5'
                      placeholder=''
                      className='form-control'
                      onChange={(e) => handleChange(e, 4)}
                      value={tempData.imagesUrl[4]}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='customFile'>
                     
                    <input
                      type='file'
                      id='customFile'
                      className='form-control'
                    />
                  </label>
                </div>
                <img src="" alt='' className='img-fluid' />
              </div>
              <div className='col-sm-8'>
                {/*<pre>{ JSON.stringify(tempData) }</pre>*/}
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='title'>
                    
                    <input
                      type='text'
                      id='title'
                      name='title'
                      placeholder=''
                      className='form-control' 
                      onChange={handleChange} 
                      value={tempData.title}
                    />
                  </label>
                </div>
                <div className='row'>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='category'>
                      
                      <input
                        type='text'
                        id='category'
                        name='category'
                        placeholder=''
                        className='form-control' 
                        onChange={handleChange} 
                        value={tempData.category}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='unit'>
                      
                      <input
                        type='unit'
                        id='unit'
                        name='unit'
                        placeholder=''
                        className='form-control' 
                        onChange={handleChange} 
                        value={tempData.unit}
                      />
                    </label>
                  </div>
                </div>
                <div className='row'>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='origin_price'>
                      
                      <input
                        type='number'
                        id='origin_price'
                        name='origin_price'
                        placeholder=''
                        className='form-control' 
                        onChange={handleChange} 
                        value={tempData.origin_price}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='price'>
                      
                      <input
                        type='number'
                        id='price'
                        name='price'
                        placeholder=''
                        className='form-control' 
                        onChange={handleChange} 
                        value={tempData.price}
                      />
                    </label>
                  </div>
                </div>
                <hr />
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='description'>
                    
                    <textarea
                      type='text'
                      id='description'
                      name='description'
                      placeholder=''
                      className='form-control' 
                      onChange={handleChange} 
                      value={tempData.description}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='content'>
                    
                    <textarea
                      type='text'
                      id='content'
                      name='content'
                      placeholder=''
                      className='form-control' 
                      onChange={handleChange} 
                      value={tempData.content}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <div className='form-check'>
                    <label
                      className='w-100 form-check-label'
                      htmlFor='is_enabled'
                    >
                      
                      <input
                        type='checkbox'
                        id='is_enabled'
                        name='is_enabled'
                        placeholder=''
                        className='form-check-input' 
                        onChange={handleChange} 
                        checked={Boolean(tempData.is_enabled)}
                      />
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='modal-footer'>
            <button type='button' className='btn btn-secondary' onClick={closeProductModal}>
              
            </button>
            <button type='button' className='btn btn-primary' onClick={submit}>
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductModal;