import { useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';
import axios from "axios";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay } from 'swiper/modules'; //  Swiper
import 'swiper/swiper-bundle.css'; //  Swiper 

import { Link } from 'react-router-dom';

function RelatedProducts() {
  const [relatedProducts, setRelatedProducts] = useState([]); // 
  const navigate = useNavigate(); // 

  // API-
  const getRelatedProducts = async (id) =>  {
    try {
      // API-
      const productRes = await axios.get(`/v2/api/${process.env.REACT_APP_API_PATH}/products`);
      console.log('RelatedProducts :', productRes);

      //  '' 
      const filteredProducts = productRes.data.products.filter(product => product.category !== '');

      setRelatedProducts(filteredProducts || []);
    } catch (error) {
      console.error(error);
    }

  };
  useEffect(() => {
    getRelatedProducts();
  }, []);


  // 
  const handleProductClick = (id) => {
    navigate(`/product/${id}`);
    window.scrollTo(0, 0);
  };
  

  return (
    <>
      <Swiper
         //navigation 
        pagination={{ clickable: true }} // 
        spaceBetween={30} //  30 
        slidesPerView={'auto'} // 4 auto
        centeredSlides={true} //  false
        loop={relatedProducts.length > 3} // 
        autoplay={{
          delay: 2500, // 
          disableOnInteraction: true, // 
        }}
        className="mt-4 mb-5"
        modules={[Navigation, Pagination, Autoplay]} // 
        breakpoints={{ // 
          767: { //  767 
            slidesPerView: 3, //  3 
            spaceBetween: 30 //  30 
          }
        }}
      >
        {/*  SwiperSlide */}
        {relatedProducts.slice(0, 6).map((product) => (
          <SwiperSlide key={product.id}>
            <div className="card border-0 mb-4 position-relative"
             onClick={() => handleProductClick(product.id)}
            >
              <Link to={`/product/${product.id}`}>
                <img 
                  src={product.imageUrl} 
                  className="card-img-top rounded-0" 
                  alt="..."
                />
              </Link>
              <div className="card-body p-0">
                <h4 className="mb-0 mt-3">
                  <Link to={`/product/${product.id}`}>
                  {product.title}
                  </Link>
                </h4>
                <p className="card-text mb-0">NT${product.price}</p>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </>
  );
};

export default RelatedProducts;